import React from "react";
import { Close } from "grommet-icons";
import { Layer, Box } from "grommet";

const Modal = ({ full, children, onClose }) => {
  return (
    <Layer full={full}>
      <Box
        width={{ min: "100%" }}
        overflow="scroll"
        pad="medium"
        margin={{ left: "auto" }}
      >
        <Close
          style={{ cursor: "pointer" }}
          color="plain"
          onClick={() => onClose && onClose()}
        />
        {children}
      </Box>
    </Layer>
  );
};

export default Modal;
